import Vue from "vue";
import Router from 'vue-router'

Vue.use(Router);

let router = new Router({
    routes: [
        {
            path: '/',
            name: 'main',
            component: () => import("../pages/mainPage")
        },
        {
            path: '/About',
            name: 'about',
            component: () => import("../pages/aboutPage"),
            props: true
        },
        {
            path: '/Skills',
            name: 'skills',
            component: () => import("../pages/skillsPage")
        },
        {
            path: '/Works',
            name: 'works',
            component: () => import("../pages/worksPage")
        }
    ]
})

export default router;